const roadApi = {
  //
  async getStaticRoadList(pagenum) {
    let query = {
      pageSize: 10,
      currentPage: pagenum,
    }
    const res = await uni.$http.get('/route/all', query)
    return res.data
  },
  
  //Id
  async getStaticRoadById(id) {
    const res = await uni.$http.get(`/route/${id}`)
    return res.data
  },
}

export default roadApi
